export default {
	data() {
		return {};
	},
	methods: {
		submitInfo: function(e) {
			console.log('form发生了submit事件，携带数据为：', e.detail.value);
			this.$Fetch({
				url: 'user/edit',
				data: e.detail.value,
				debug: true,
				success: res => {
					uni.showToast({
						title: '修改成功',
						icon: 'success'
					});
					setTimeout(() => {
						uni.navigateBack({
							delta: 1
						});
					}, 1500);
				}
			});
		}
	}
};
